// Custom Alert template for react-alert
import React, { memo } from 'react'
import cx from 'clsx'
import PropTypes from 'prop-types'
import {
  CheckCircleIcon, InformationCircleIcon, XCircleIcon, XMarkIcon,
} from '@heroicons/react/24/solid'

// Define the prop types for the component
interface IAlert {
  /* (string): The message to be displayed in the alert. */
  message: string,
  //  (object): An object containing the options for the alert, including the type property, which can have the values 'info', 'success', or 'error'.
  options: {
    type: 'info' | 'success' | 'error',
  },
  // (string): Additional CSS classes to be applied to the alert container.
  className: string,
  // (function): A callback function to be called when the alert is closed.
  close: () => void,
}

// AlertTemplate component
const AlertTemplate = ({
  message, options, className, close,
}: IAlert): JSX.Element => {
  // Determine the type of the alert
  const { type } = options
  const isInfo = type === 'info'
  const isSuccess = type === 'success'
  const isError = type === 'error'

  return (
    <div
      className={cx('flex justify-between items-center mb-5 mr-2 rounded-md p-4 z-50 shadow-sm w-96 pointer-events-auto', {
        'bg-green-50': isSuccess,
        'bg-blue-50': isInfo,
        'bg-red-50': isError,
      }, className)}
    >
      <div className='flex-shrink-0'>
        {isInfo && <InformationCircleIcon className='h-5 w-5 mr-2 text-blue-400' />}
        {isSuccess && <CheckCircleIcon className='h-5 w-5 mr-2 text-green-400' />}
        {isError && <XCircleIcon className='h-6 w-6 mr-2 text-red-400' />}
      </div>
      <span
        className={cx('text-sm font-medium uppercase', {
          'text-green-800': isSuccess,
          'text-blue-700': isInfo,
          'text-red-800': isError,
        })}
      >
        {message}
      </span>
      <button
        onClick={close}
        type='button'
        className={cx('inline-flex cursor-pointer rounded-md p-1.5 focus:outline-none focus:ring-2 focus:ring-offset-2', {
          'bg-green-50 text-green-500 hover:bg-green-100 focus:ring-offset-green-50 focus:ring-green-600': isSuccess,
          'bg-blue-50 text-blue-500 hover:bg-blue-100 focus:ring-offset-blue-50 focus:ring-blue-600': isInfo,
          'bg-red-50 text-red-500 hover:bg-red-100 focus:ring-offset-red-50 focus:ring-red-600': isError,
        })}
      >
        <span className='sr-only'>Dismiss</span>
        <XMarkIcon className='h-5 w-5' />
      </button>
    </div>
  )
}

// Define prop types for the component
AlertTemplate.propTypes = {
  message: PropTypes.string,
  className: PropTypes.string,
  options: PropTypes.shape({
    type: PropTypes.oneOf(['info', 'success', 'error']),
  }),
  close: PropTypes.func,
}

// Define default props for the component
AlertTemplate.defaultProps = {
  message: '',
  className: '',
  options: {},
  close: () => { },
}

export default memo(AlertTemplate)
